export default {"ts":"import { Component } from 'jinge';\n\nimport _tpl from './basic.html';\n\nexport default class ExampleStates extends Component {\n  static template = _tpl;\n}\n","sass":".states-demo {\n  background: var(--demo-states-background);\n  padding: 16px;\n  display: flex;\n  flex-wrap: wrap;\n\n  :global {\n    .md-state-container {\n      width: 200px;\n      height: 200px;\n      margin: 24px;\n      display: flex;\n      align-items: center;\n      justify-content: center;\n    }\n  }\n}\n","html":"<!-- import style from './basic.scss' -->\n<div :class=\"style.statesDemo\">\n  <div class=\"md-state-container md-content-on-surface\">\n    <i class=\"md-state-overlay\"/>\n    <span class=\"md-state-content\">\n      Content: On Surface\n    </span>\n  </div>\n  <div class=\"md-state-container md-content-is-primary\">\n    <i class=\"md-state-overlay\"/>\n    <span class=\"md-state-content\">\n      Content: Primary\n    </span>\n  </div>\n  <div class=\"md-state-container md-content-on-primary\">\n    <i class=\"md-state-overlay\"/>\n    <span class=\"md-state-content\">\n      Content: On Primary\n    </span>\n  </div>\n</div>"}